export default {
	props: {
		item: Object,
		index: {
			type: Number,
			default: 0
		},
		index1: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			
		};
	},
	methods: {
		// 关注
		async follow () {
			try{
				let [err,res] = await this.$http.post('/follow',{
					follow_id:this.item.userid
				},{
					token:true,
					checkToken:true,
					checkAuth:true
				});
				// 错误处理
				if (!this.$http.errorCheck(err,res)){
					return;
				}
				// 通知首页修改数据
				uni.showToast({ title: '关注成功' });
				let resdata = {
				 	type:"guanzhu",
				 	userid:this.item.userid,
				 	data:true
				};
				// 通知父组件
				this.$emit('changeEvent',resdata);
				// 通知首页
				uni.$emit('updateData',resdata);
			}catch(e){ return; }
		},
		// 赞 踩
		async operate (type) {
			try{
				let index = (type === 'smile') ? 1 : 2; // 操作后的状态
				if(this.item.infonum.index === index) return; // 状态相同不修改
				let [err,res] = await this.$http.post('/support',{
					post_id:this.item.id,
					type:index-1
				},{
					token:true,
					checkToken:true,
					checkAuth:true
				});
				// 错误处理
				if (!this.$http.errorCheck(err,res)) return;
				uni.showToast({
					title: index == 1 ? "顶成功" : "踩成功"
				});
				let resdata = {
					type:"support",
					post_id:this.item.id,
					do:type
				};
				// 通知父组件
				this.$emit('changeevent',resdata);
				// 通知全局
				uni.$emit("updateData",resdata);
			}catch(e){ return; }
		},
		// 进入详情页
		openDetail () {
			uni.navigateTo({
				url:'../../pages/detail/detail?detailData='+JSON.stringify(this.item)
			})
		}
	}
}
